@import 'normalize';
@import 'typography';
@import 'footer';
@import 'logo';
/*
====================================
Default Styling
====================================
*/

@font-face {
  font-family: 'TSV Sans';
  src: url('fonts/TSVSans.otf');
  font-weight: normal;
      font-style: normal; 
}

@mixin fontSize($sizeValue){
    font-size: $sizeValue * 1px;
    font-size: ($sizeValue/10) * 1rem;
}

body {
	font-family: 'TSV Sans', sans-serif;
	background: #fffeb5;
	@include fontSize(9);
	outline: none;
}
main {
	max-width: 1440px;
	padding: 0 2.5%;
	margin: 0 auto;
}
.headerMain, .footerContainer {
	padding-top: 2.5%;
}
.footerContainer {
	padding-bottom: 2.5%;
}
.flex {
	display: flex;
	flex-flow: row wrap;
}
h1 {
	text-transform: uppercase;
	@include fontSize(9);
}
.pageTitle {

	margin: 0 0 2.5%;
	padding: 5px 0;
	text-align: center;
	width: 100%;
	background: #eb5858;
	border-bottom: 1px solid black;
	border-left: 1px solid black;
	border-right: 1px solid black;

}
h2 {
	@include fontSize(9);
	font-weight: 300;
	text-transform:uppercase;
}
a {
	text-decoration: none;
	color: black;
}

img {
	max-width: 100%;
}

//header
.headerMain {
	@extend .flex;
	button {
		@include fontSize(9);
		text-transform: uppercase;
		height: 30px;
		background: white;
		border: 1px solid black;
		border-left: 0;
		outline: none;
		width: 16%;
	}
}
.inspire {
	position: relative;
	width: 45%;
	border: 1px solid black;
	background: white;
	h1 {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		margin: 0 auto;
		max-width: 200px;
		text-align: center;
		font-size: 1.8vw;
	}
}
//menu 
#menu-header-menu {
	@include fontSize(9);
	list-style: none;
	display: flex;
	margin: 2.5% 0 0;
	padding: 0;
	justify-content: space-between;
	width: 100%;
	li {
		border: 1px solid black;
		background: white;
		text-transform: uppercase;
		padding: 5px;
		width:150px;
		width: 15.7vw;
		text-align: center;
		border-left:none;
		&:hover {
			background:#eb5858;
		}
	}
	li:nth-child(1) {
		border-left:1px solid black;
	}
	.current-menu-parent,.current-menu-item {
		background: #eb5858;
	}
}

ul.sub-menu {
	list-style: none;
	margin: 0 -6px 0;
	padding: 6px 0 0;
	position: absolute;
	li {
		border-top: none !important;
		border-left: 1px solid black !important;
	}
	// left: -0.5vw;
}

#menu-header-menu li  > ul.sub-menu {
  display: none;
}
#menu-header-menu li:hover > ul.sub-menu {
  display: block;
}


//footer 

.footerContainer {
	@extend .flex;
	justify-content:space-between;
	h2 {
		text-align: center;
		background: #a3a1e9;
		padding: 5px 0;
		border-bottom: 1px solid black;
		width: 100%;
		margin: 0;

	}
	.contact,.landAcknowledgement,.footermenu,.mainlingList {
		width: 24%;
		border: 1px solid black;
		background: white;
		height: fit-content;
	}
}
.galleryAddress, .officeHours, .galleryHours {
	padding: 10px;
	border-bottom: 1px solid black;
}
.linksEach {
	list-style: none;
	padding: 10px;
	margin: 0;
	@extend .flex;
	@include fontSize(15);
	li {
		margin-right: 5px;
	}
}
.acknowledgementInner {
	padding: 10px;
}
#menu-footer-menu {
	list-style: none;
	padding: 0;
	margin: 0;
	text-align: center;
	text-transform: uppercase;
	li {
		padding: 5px;
		border-bottom: 1px solid black;
		&:hover {
			background:#eb5858;
		}
	}
	li:last-child {
		border-bottom: none;
	}
}

.mc-field-group {
	label {
		display: none;
	}
	input {
		width: 100%;
		border: none;
		border-bottom: 1px solid;
		border-radius: 0;
		padding: 5px;
		outline:none;
	}
}
	.button {
		border: none;
		width: 100%;
		text-align: center;
		text-transform: uppercase;
		background: #75fb4c;
		padding: 5px;
	}
.funders {
	margin-top: 2.5%;
	@extend .flex;
	background:white;
	border: 1px solid black;
	justify-content:space-around;
	align-items: center;
	img {
		width: 25%;
		height: fit-content;
	}
}
// memebership
.membershipPage {
	@extend .flex;
	justify-content:space-between;
}
.membershipInfoBox {
	width: 100%;
	padding: 10px;
	background: white;
	border: 1px solid black;
	margin-bottom: 2.5%;

}
.membershipOptionBox {
	width:49%;
	height: fit-content;
	margin-bottom:2.5%;
	border: 1px solid black;
	h2 {
		text-align: center;
		background: #a3a1e9;
		padding: 5px 0;
		border-bottom: 1px solid black;
		width: 100%;
		margin: 0;

	}
	.membershipBoxInfo {
		background: white;
		padding: 5px;
		border-bottom: 1px solid black;
	}
	.membershipBoxPrice {
		background: #75fb4c;
		padding: 5px;
		text-align: center;
	}
}

.membershipOptionsTable {
	margin-bottom: 2.5%;
	width: 100%;

	table {
		width: 100%;
		text-align: center;
		thead tr th:nth-child(n+2) {
			background: #a3a1e9;
			padding: 5px;
			border-top: 1px solid black;
			border-left: 1px solid black;
		}
		thead tr th:last-child {
			border-right: 1px solid black;
		}
		// thead tr th::first-line {
		// 	background: #a3a1e9;
		// 	width: 100%;
		// 	padding: 1px 5px;
		// }
		tbody tr td:nth-child(1){
			text-align: left;;
		}
		tbody{
			// border: 1px solid black;
			background: white;
			tr td {
				border: 1px solid black;
			}
		}
	}
}





